.App {
    height: 100vh;
}

.Cart {
    position: relative;

}
.Cart div {
    height: 100%;
    overflow-y: scroll;
}
.Cart table {
    width: 100%;
    margin-bottom: 55px;
}
.Cart table thead {
    font-weight: bold;
    position: sticky;
    top: 0;
    background-color: white;
}
.Cart table thead td {
    padding-top: 0;
}
.Cart table tr {
    cursor: pointer;
}
.Cart table tr:nth-child(even) {
    background: lightgreen;
}
.Cart table td.quantity {
    text-align: right;
}
.Cart table td.price, .Cart div.sum.price {
    font-variant-numeric: tabular-nums;
    text-align: right;
}
.Cart div.sum.price {
    height: 18px;
    padding-right: 2px;
    font-weight: bold;
    border-top: black 1px solid;
    position: absolute;
    overflow: visible;
    right: 0;
    bottom: 0;
    width: 100%;
    background-color: white;
}

.Controls {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 0 0;
}

.Controls button {
    display: grid;
    grid-template-rows: 2fr 1fr;
    grid-gap: 0 0;
    align-items: center;
    font-size: 1.1em;
    padding: 0;
}
.Controls button .name {
    font-weight: bold;
    padding-top: 10px;
    word-break: break-word;
}
.Controls button .amount {
    font-size: 0.7em;
}
.Controls button.soft {
    background-color: green;
    color: white !important;
}
.Controls button.medium {
    background-color: yellow;
}
.Controls button.hard {
    background-color: red;
}


@media (orientation: landscape) {
    .App {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 0 0;
    }
}

@media (orientation: portrait) {
    .Cart {
        height: 50vh;
    }
    .Controls {
        height: 50vh;
    }
}
